.intro {
  display: flex;
  margin-top: 120px;
  margin-bottom: 120px;
  padding-left: 103px;
  padding-right: 103px;

  @media (min-width: $beyond-desktop-width) {
    max-width: 1160px;
    margin-left: auto;
    margin-right: auto;
  }

  @media (max-width: $tablet-max-width) {
    align-items: start;
    margin-top: 100px;
    margin-bottom: 95px;
    padding-left: 45px;
    padding-right: 45px;
  }

  @media (max-width: $mobile-max-width) {
    flex-direction: column;
    align-items: center;
    margin-top: 66px;
    margin-bottom: 65px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.intro__photo-wrapper {
  width: 50%;
  height: 301px;
  box-sizing: border-box;
  padding-left: 20px;

  @media (max-width: $tablet-max-width) {
    height: 264px;
    padding-left: 15px;
  }

  @media (max-width: $mobile-max-width) {
    width: auto;
    height: 186px;
    padding-left: 0;
    margin-top: 34px;
  }
}

.intro__photo {
  height: 301px;
  width: 560px;
  object-fit: cover;

  @media (max-width: $tablet-min-width) {
    width: 324px;
    height: 264px;
  }

  @media (max-width: $mobile-max-width) {
    width: 290px;
    height: 186px;
  }
}

.intro__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin: 0;
  margin-top: 10px;
  margin-bottom: 25px;

  @media (max-width: $tablet-max-width) {
    font-size: 26px;
    line-height: 36px;
    margin-top: -5px;
    margin-bottom: 15px;
  }

  @media (max-width: $mobile-max-width) {
    font-size: 24px;
    line-height: 34px;
  }
}

.intro__text {
  margin: 0;
}

.intro__text-wrapper {
  width: 50%;
  box-sizing: border-box;
  padding-right: 68px;
  margin-top: 45px;

  @media (max-width: 1200px) {
    margin-top: -5px;
    padding-right: 30px;
  }

  @media (max-width: $tablet-max-width) {
    padding-right: 15px;
  }

  @media (max-width: $mobile-max-width) {
    width: auto;
    padding-right: 0;
  }
}
